import React, { useMemo } from "react";
import { useEffect, useState } from "react";
import CommonServerice from "src/services/CommonService";
import { parseQueryParams, pushQueryParams } from "src/utils/routerHelper";

const { default: EntityForm } = require("../EntityForm")

const LocationFilter = ({
    label = "Filter by location",
    cols = 12,
    ...rest
}) => {
    const [locations, setLocations] = useState([])
    const [locationId, setLocationId] = useState(null)

    const getFilterValue = () => {
        return parseQueryParams(rest.history, 'locationId', null)
    }

    useEffect(() => {
        (async () => {
            setLocations(await CommonServerice.getLocationList())
        })();
        setLocationId(getFilterValue())
    }, [])

    useEffect(() => {
        const unlisten = rest.history.listen((...args) => {
            setLocationId(getFilterValue())
        })
        return unlisten
    }, [])

    const initialValues = useMemo(() => {
        return { locationId: locationId || "" }
    }, [locationId])

    return (
        <EntityForm
            hideSubmit={true}
            initialValues={initialValues}
            fields={[
                {
                    id: 'locationId',
                    name: 'locationId',
                    label,
                    type: 'select',
                    cols,
                    hideLabel: false,
                    onChange: (e) => {
                        const value = e.nativeEvent.target.value
                        pushQueryParams(rest.history, {
                            locationId: value || null
                        }, true)
                    },
                    options: locations.map(l => {
                        return {
                            key: l.id,
                            value: l.title
                        }
                    })
                }
            ]}
        />
    )
}

export default LocationFilter